import {motion} from 'framer-motion'
import {
    mainLineAnimation,
    subLineAnimation,
    polygonAnimation,
    dotAnimation,
    textAnimation,
    basicKashfAnimation, flowerAndFrameAnimation
} from '../../../animations/kashf';

import {
    opacityAnimation,
    backgroundAnimationMobile,
    bottomLineAnimationMobile,
    boxAnimationMobile,
    aboutAnimationMobile,
} from '../../../animations/alefba';
import {noneAnimation, noneAnimationAlefba} from "../../../animations/main-page";
import {flowerAnimation} from "../../../animations/kashf-image";

const animationChooser = (scrollQuantity) => {

    switch (scrollQuantity) {
        case 2:
            return {
                'opacityAnimation':opacityAnimation,
                'backgroundAnimationMobile':backgroundAnimationMobile,
                'bottomLineAnimationMobile':bottomLineAnimationMobile,
                'boxAnimationMobile':boxAnimationMobile,
                'aboutAnimationMobile':aboutAnimationMobile,
                'flowerAndFrameAnimation':flowerAndFrameAnimation,
                'mainLineAnimation': mainLineAnimation,
                'subLineAnimation': subLineAnimation,
                'polygonAnimation': polygonAnimation,
                'dotAnimation': dotAnimation,
                'textAnimation': textAnimation,
                'basicKashfAnimation':basicKashfAnimation,
                'flowerAnimation':flowerAnimation,
            };
        default:
            return {
                'opacityAnimation':noneAnimation,
                'backgroundAnimationMobile':noneAnimation,
                'bottomLineAnimationMobile':noneAnimation,
                'boxAnimationMobile':noneAnimation,
                'aboutAnimationMobile':noneAnimation,
                'flowerAndFrameAnimation':noneAnimation,
                'mainLineAnimation': noneAnimation,
                'subLineAnimation': noneAnimation,
                'polygonAnimation': noneAnimation,
                'dotAnimation': noneAnimation,
                'textAnimation': noneAnimation,
                'basicKashfAnimation':noneAnimation,
                'flowerAnimation':noneAnimation,
            };
    }
};

